

const modeDev = process.env.NODE_ENV === "development";

  const CLIENT_ID = modeDev ? 
    process.env.REACT_APP_OAUTH_CLIENT_ID_DEV :
    process.env.REACT_APP_OAUTH_CLIENT_ID;

  export const REDIRECT_URI = modeDev ? 
    'http://localhost:3000' :
    'https://sunyrora.github.io/react.blog.test';

  export const GATE_KEEPER = modeDev ? 
    'http://localhost:9999' : 
    'https://gate-keeper-sunyrora.herokuapp.com';

export const GIT_AUTH_URI = `https://github.com/login/oauth/authorize?client_id=${CLIENT_ID}&scope=user&redirect_uri=${REDIRECT_URI}`;



