import { Component } from 'react';
import storage from '../utils/storage';
import { GATE_KEEPER } from '../config/github';

class GitAuthCallback extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isReady: false,
    };
  }

  componentDidMount() {
    const token = storage.get('gitToken');

    console.log('token: ', token);

    if(!token || token === 'undefined') {
      return this.gitAuth();
    }

    this.setReady();
  }

  gitAuth() {
    const code =
      window.location.href.match(/\?code=(.*)/) &&
      window.location.href.match(/\?code=(.*)/)[1];
    if (code) {
      fetch(`${GATE_KEEPER}/authenticate/${code}`)
        .then(response => response.json())
        .then(({ token }) => {
          // console.log('gitAuth: token: ', token);
          storage.set('gitToken', token);
          this.setReady();
        });
    }
  }

  setReady() {
    console.log('Ready??');
    this.setState({ isReady: true });
  }


  render() {
    if(this.state.isReady) {
      window.location.replace('/');
      // window.location.reload();
    }

    return null;
    // return this.state.isReady ? <Redirect to='/' /> : null;
  }
}

export default GitAuthCallback;



